import { Dispatch, SetStateAction, useEffect, useRef, useState } from 'react';

import * as StompJs from '@stomp/stompjs';

import { AuthApi, baseURL, fetchDefaultChatRoomId, headers } from '@/api/api-util';
import { useAppDispatch, useAppSelector } from '@/store/hooks';
import { defaultChatRoomMessagePush, setChatHistory } from '@/store/reducers/defaultChatRoomSlice';

interface PageChatRoomProps {
  roomId: string;
  setRoomId: Dispatch<SetStateAction<string>>;
}

function PageChatRoom({ roomId, setRoomId }: PageChatRoomProps) {
  const dispatch = useAppDispatch();

  const chatRef = useRef<HTMLDivElement[]>([]);
  const client = useRef<StompJs.Client | null>(null);
  const [sender, setSender] = useState('');
  const [loginId, setLoginId] = useState('');
  const [message, setMessage] = useState('');
  const messages: {
    type: string;
    sender: string;
    message: string;
    createAt: string;
    loginId: string;
    id: number;
  }[] = useAppSelector((state) => state.defaultChatRoom);

  useEffect(() => {
    if (!localStorage.getItem('user')) return;

    const fetchData = async () => {
      try {
        const roomId = await fetchDefaultChatRoomId();
        if (roomId) {
          setRoomId(roomId);
          const res = await AuthApi.get(`/chat/roomId/${roomId}`);
          dispatch(setChatHistory(res.data));
        }
      } catch (err) {
        console.error(err);
      }
    };

    fetchData();
  }, [dispatch, setRoomId]);

  useEffect(() => {
    const user = localStorage.getItem('user');
    let userData: any;
    if (user) {
      userData = JSON.parse(user);
    }
    if (!userData) {
      return;
    } else {
      setSender(userData.name);
      setLoginId(userData.loginId);
    }
    connect();

    return () => {
      if (client.current) {
        disconnect();
      }
    };
  }, [roomId]); // roomId   

  useEffect(() => {
    handleScroll(chatRef.current[messages.length - 1]);
  }, [messages]); // messages   

  const connect = () => {
    client.current = new StompJs.Client({
      connectHeaders: headers,
      brokerURL: `ws://${baseURL}/ws-stomp/websocket`, //    
      reconnectDelay: 5000,
      // webSocketFactory: () => new SockJS('/ws-stomp'), // proxy  
      heartbeatIncoming: 4000,
      heartbeatOutgoing: 4000,
      onConnect: () => {
        subscribe();
      },
      onStompError: (frame) => {
        console.error(frame);
      },
    });

    client.current.activate();
  };

  const disconnect = () => {
    if (client.current) {
      client.current.deactivate();
    }
  };

  const subscribe = () => {
    if (client.current) {
      client.current.subscribe(
        `/sub/chat/room/${roomId}`,
        (message) => {
          const newMessage = JSON.parse(message.body);
          dispatch(defaultChatRoomMessagePush([newMessage]));
        },
        headers,
      );
    }
  };

  const publish = (e: any) => {
    e.preventDefault();
    if (client.current && client.current.connected) {
      client.current.publish({
        destination: '/pub/chat/message',
        body: JSON.stringify({
          type: 'TALK',
          roomId: roomId,
          sender: sender,
          message: message,
          loginId: loginId,
        }),
        headers,
      });
    }
    setMessage('');
  };

  const handleScroll = (ref: HTMLDivElement | null) => {
    if (ref) {
      ref.scrollIntoView({ behavior: 'smooth', block: 'end' });
    }
  };

  return (
    <div className="h-full w-full p-5" style={{ backgroundColor: '#1e1e1e' }}>
      <div className="w-full mb-5 overflow-y-scroll scrollbar-hide" style={{ height: 'calc(100% - 70px)' }}>
        {messages.map((message, index) => (
          <div
            ref={(el: HTMLDivElement) => {
              chatRef.current[index] = el;
            }}
            key={index}
            className={`${message.loginId === loginId ? 'text-right' : ' text-left'}  text-white text-sm`}
          >
            {message.sender} - {message.message}
          </div>
        ))}
      </div>
      <div className="w-full h-[50px] rounded-[20px]" style={{ backgroundColor: '#2F3336' }}>
        <form className="w-full h-full flex justify-center items-center" onSubmit={publish}>
          <input
            className=" flex-grow h-[44px] text-white caret-white px-2 rounded-l-[20px] "
            style={{ backgroundColor: '#2F3336' }}
            value={message}
            placeholder=" ."
            onChange={(e) => {
              setMessage(e.target.value);
            }}
          />
          <button
            className="h-full w-[80px] text-white  flex justify-center items-center"
            style={{ borderLeft: 'solid 1px #1e1e1e' }}
          >
            
          </button>
        </form>
      </div>
    </div>
  );
}
export default PageChatRoom;
